import { AgGridModule } from '@ag-grid-community/angular';
import { Component, OnInit, NgModule, Input, Output, EventEmitter } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AllCommunityModules, Module } from '@ag-grid-community/all-modules';






@Component({
  selector: 'app-data-grid',
  templateUrl: './data-grid.component.html',
  styleUrls: ['./data-grid.component.css']
})
export class DataGridComponent implements OnInit {
  modules: Module[] = AllCommunityModules;
  searchValue: string;
  private gridApi;
  private gridColumnApi;
  set: Set<string> = new Set<string>();
  rowData: any[];
  comptadorCanvis: number;
  comptadorRedo: number;
  @Input() columnDefs: any[];
  @Input() getAll: () => Observable<any>;
  @Output() remove: EventEmitter<any[]>;
  @Output() new: EventEmitter<boolean>;
  @Output() sendChanges: EventEmitter<any[]>;


  constructor() {

    this.remove = new EventEmitter();
    this.new = new EventEmitter();
    this.sendChanges = new EventEmitter();
    this.comptadorCanvis = 0;
    this.comptadorRedo = 0;
  }

  
  ngOnInit() {
  }

  onGridReady(params){
    this.gridApi = params.api;
    this.gridColumnApi = params.columnApi;
    this.gridApi.rowHeight = 100;
    this.getElements();
    this.gridApi.sizeColumnsToFit();

  }

  quickSearch(){
      this.gridApi.setQuickFilter(this.searchValue);
  }

  getElements()
  {
    this.getAll()
    .subscribe((items) => {
        console.log(items);
        this.rowData = items;
    });
  }

  removeData() {
    this.gridApi.stopEditing(false);
    const selectedNodes = this.gridApi.getSelectedNodes();
  	const selectedData = selectedNodes.map(node => node.data);
    this.remove.emit(selectedData);
}

  newData()
  {
    this.gridApi.stopEditing(false);
    this.new.emit(true);
  }

  applyChanges()
  {
    const itemsChanged: any[] =[];
    this.gridApi.stopEditing(false);
    this.set.forEach( id => itemsChanged.push(this.gridApi.getRowNode(id).data));
    this.sendChanges.emit(itemsChanged);
    this.set.clear();
    this.comptadorCanvis=0;
    this.comptadorRedo=0;
  }


  onCellEditingStopped(e)
  {
    this.set.add(e.node.id);
    this.comptadorCanvis += 1;
    this.comptadorRedo=0;
  }

  deleteChanges()
  {
    for(let i = 0; i <= this.comptadorCanvis; i++)
    {
      this.gridApi.undoCellEditing();
    }
    this.set.clear();
    this.comptadorCanvis = 0;
    this.comptadorRedo=0;
  }



  undo() {
    this.gridApi.stopEditing(false);
    this.gridApi.undoCellEditing();
    if (this.comptadorCanvis > 0) 
    { this.comptadorCanvis -= 1;
      this.comptadorRedo += 1;
     }
  }

  redo() {
    this.gridApi.stopEditing(false);
    this.gridApi.redoCellEditing();
    this.comptadorCanvis += 1;
    this.comptadorRedo -= 1;
  }


}
